}

.grid-container {
  display: grid;
  grid-column-gap: 5px;
  grid-template-columns: auto auto auto auto;
  justify-content: center;
}

.grid-item {
  width: auto;
  /* width: calc((100vw - 300px) / 4); */
  height: 450px;
  margin-top: 8px;
  padding-left: 8px;
  /* padding-right: 8px; */
}

.grid-img {
  width: inherit;
  object-fit: cover;
  height: inherit;
}

.scrollView {
  height: calc(100vh - 125px);
  overflow-y: auto;
  padding-bottom: 100px;
  scrollbar-width: thin;
}

::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}

::-webkit-scrollbar-button {
  background: #ccc;
}
::-webkit-scrollbar-track-piece {
  background: #888;
}

.profile_img {
  height: 140px;
  width: 140px;
  border-radius: 50%;
}
